header {
    height: 65vh;
    padding-top: 5rem;
    overflow: hidden;
    /* border-bottom: dotted;
    border-color: black; */
}
 
.header__container {
    /* //height: 100%; */
    position: relative;
    text-align: center;
    margin-bottom: 1rem;
    
}

.CTA {
    display: flex;
    gap: 0.1rem;
    justify-content: center;
}
.header__socials {
    display: flex;
    margin: 0.5rem;
    position: absolute;
    left: calc(50% - 3rem);
    gap: 1.2rem;
    font-size: 20px;
}

.header__socials::after {
    content: "";
    width: 1px;
    height: 2rem;
}

