 .avatar {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    width: 190px; /* Default width */
    height: 200px; /* Default height */
    border-radius: 50%;
    background: linear-gradient(#EEE9DA, transparent);
    left: calc(50% - 6rem);
    margin-top: 5rem;
    /* overflow: hidden; */
    
  }
  
  .img {
    max-width: 100%;
    max-height: 100%;
    border-radius: 50%;
    margin-top: 1rem;
    object-fit: contain;
  }
  
  .scroll-down {
    position: absolute;
    right: -2.3rem;
    bottom: -10rem;
    transform: rotate(90deg);
    font-weight: 200;
    color: var(--color-primary)
  }







