@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,300;0,600;1,400&display=swap');

*{
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: 0;
    list-style: none;
    text-decoration: none;
}

:root {
    --color-h1: #1bb5ed;
    --corlor-bg: #0e3a57;
    --corlor-bg-variant: #459ecd;
    --color-primary: #3fabd2;
    --color-white: white;
    --color-light: rgba(255, 251, 251, 0.625);
    
    --container-width-lg: 75%;
    --container-width-md: 85%;
    --container-width-sm: 90%;
    
    --transition: all 400ms ease;


}

html {
    scroll-behavior: smooth;
}

::-webkit-scrollbar {
    display: none;
}

body {
    opacity: 1;
    font-family: 'Poppins', sans-serif;
    background-color: var(--corlor-bg);
    background-image: url("https://www.transparenttextures.com/patterns/blu-stripes.png");
    line-height: 1.5;
}

.container {
    width: var(--container-width-lg);
    margin: 0 auto;
}

section {
   
    margin-top: 4rem;
}

h1, h2, h5 {
    color: var(--color-white);
    text-align: center;
}

h1, h2 {
    color: var(--color-h1)
}

a {
    transition: var(--transition);
    color: var(--color-primary);
}

a:hover {
    color: var(--color-white);
}


.btn {
    margin: 0.5rem;
    width: max-content;
    display: inline-block;
    color: var(--color-white);
    padding: 0.75rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border: 1px solid var(--color-primary);
    transition: var(--transition);
    
}
.btn:hover {
    background: var(--corlor-bg-variant);
    border-color: transparent;
}

.btn-primary {
    color: black;
    background-color: var(--color-primary);
}

img {
    display: block;
    width: 100%;
    object-fit: cover;
}

/*====================== MEDIA QUERIES ==========================*/

@media screen and (max-width: 1024px) {
    .container {
        width: var(--container-width-md);
    }
}

@media screen and (max-width: 600px) {
    .container {
        width: var(--container-width-sm);
    }
}

